import { PlaywrightBlocker } from "@cliqz/adblocker-playwright";
import { Page } from "@playwright/test";

export class bookstorelogin {

    readonly page: Page;
    readonly username: string;
    readonly password: string;

    constructor(page: Page, username: string, password: string) {
        this.page = page;
        this.username = username;
        this.password = password;
    }

    async handelAdds(){
          PlaywrightBlocker.fromPrebuiltAdsAndTracking(fetch).then(blocker => {
            blocker.enableBlockingInPage(this.page);
          })
    }

    async navigatetoURL() {
        await this.page.goto('https://demoqa.com/login');
    }

    async login() {
        await this.page.fill('#userName', this.username);
        await this.page.fill('#password', this.password);
        await this.page.click('#login');
    }

    async clickConsent(){
        await this.page.getByLabel('Consent', { exact: true }).click();
    }

    async logout(){
        await this.page.click('#submit')
    }
}
